</template>


<script>
import Preloader from '@/components/Preloader/Preloader'
import SwiperProductCategory from '@/components/Swipers/SwiperProductCategory'
import ProductsItem from '@/components/Products/ProductsItem'

import Vue from 'vue';
import Tabs from 'vue-tabs-component';

Vue.use(Tabs);

export default {
    name: "ProductsPage",
    props: ["id", "category_id", "product_id"],
    components: {
        Preloader, SwiperProductCategory, ProductsItem
    },
    data() {
        return {
            products: [],
            product: {},
            category: {},
            productInfo: {},
            loading: true,
            currentTab: "parameters_tab",
            similarProducts: []
        }
    },
    created() {
        this.getProduct(this.id)
    },
    mounted() {
        
    },
    computed: {
        
    },
    methods: {
        tabChanged(selectedTab) {
            this.currentTab = selectedTab.tab.id;
        },
        changeProduct(id) {
            console.log(id);
            this.getProduct(id);
        },
        getProduct(id) {
            this.loading = true;
            this.axios
                .get('/static/products.json')
                .then(response => {
                    if (id == 'undefined') {
                        return Promise.reject();
                    }
                    this.products = response.data.data;
                    this.product = this.products.find((item) => {
                        return item.id == id
                    });
                    this.category = this.product.categories.find((item) => {
                        return item.id == this.category_id
                    });
                    this.category.products.map((item) => {
                        item.items.find((item) => {
                            if (item.id == this.product_id) {
                                this.productInfo = item;
                            } else {
                                this.similarProducts.push(item);
                            }
                        })
                    });
                    this.loading = false;
                }).catch(error => {
                    this.$router.push({ name: 'PageNotFound' });
                });
        }
    }
}
</script>

<style lang="scss">
    .product-page {
        &__image {
            display: block;
            width: 100%;
            height: auto;
            min-height: 400px;
            background-position: top center;
            background-repeat: no-repeat;
            background-size: contain;
        }
        &__stock {
            background: $white;
            border-radius: 20px;
            padding: 24px;
        }
        &__state {
            display: flex;
            & svg,
            & img {
                width: 32px;
                height: 32px;
            }
        }
    }
</style>